body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
	background: #fff;
	border-radius: 2px;
	height: 200px;
	margin: 1rem;
	width: 200px;
	box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
	height: 100%;
	overflow: hidden;
}

.card > .img-container > img {
	width: 100%;
}
.wrapper {
	background: #ECEFF1;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row wrap;
	        flex-flow: row wrap;
	padding: 20px;
	-webkit-align-content: flex-start;
	        align-content: flex-start;
	overflow: auto;
}

.score-board {
	display: block;
	width: 100%;
	text-align: center;
	font-size: 30px;
	text-decoration: underline;
	color: #2A77AE;
	margin-bottom: 20px;
}
  
.modal-wrapper {
	position: absolute;
	z-index: 10;
	height: 90%;
	width: 97%;
	margin: auto;
	background: white;
	border: 1px solid #d0cccc;
	box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
	transition: all .8s;
}

.modal-image {
	max-width: inherit;
	height: auto;
}

.modal-header {
	background: #263238;
	height: 40px;
	line-height: 40px;
	padding: 5px 20px;
	text-align: right;
}

.modal-header h3 {
	color: white;
	float: left;
	margin: 0;
	padding: 0;
}

.modal-body {
	padding: 10px 15px;
	text-align: center;
}

.modal-footer {
	background: #263238;
	height: 25px;
	padding: 15px;
}

.close-modal-btn {
	color: white;
	cursor: pointer;
	float: right;
	font-size: 30px;
	margin: 0;
}

.close-modal-btn:hover {
	color: black;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

